/*Text.css*/
.header-text {
	max-width: 56rem;
	text-align: left;
	color: var(--smoke);
}
h1, h2, h3, h4, h5 {
	margin: 0;
	font-family: 'Sanchez', serif;
	color: inherit;
	font-weight: 400;
}
h1 {
	font-size: 64px;
	font-weight: 300;
	line-height: 80px;
	margin-bottom: 24px;
}
h2 {
	font-size: 32px;
	line-height: 40px;
	margin-bottom: 32px;
}
h3 {
	font-size: 24px;
	margin-bottom: 16px;
}
h4 {
	font-size: 24px;
}
h5 {
	font-size: 20px;
}
p {
	margin: 0 0 16px 0;
	font-family: 'Open Sans', sans-serif;
	font-size: 18px;
}
p.sm {
	font-size: 14px;
}
p.md {
	font-size: 20px;
}
p.bold {
	font-weight: 700;
}
hr {
	border-radius: 2px;
	margin: 24px auto;
}
.accent-blue {
	color: var(--blue-lt);
}
.accent-blue-dk {
	color: var(--blue-dk);
}
.accent-purple {
	color: var(--purple-lt);
}
.accent-purple-dk {
	color: var(--purple-dk);
}
.accent-green {
	color: var(--green-lt);
}
.accent-green-dk {
	color: var(--green-dk);
}
.accent-brown {
	color: var(--brown-lt);
}
.accent-brown-dk {
	color: var(--brown-dk);
}

/*special rules*/
span > h3 {
    display: inline-block;
    margin-right: 8px;
}
a p {
	margin: 0;
}

